.root {
	border-radius: 4px;
	background-color: var(--ui-bg);
	color: var(--text);
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
	-webkit-box-shadow: var(--ui-dp-2);
	        box-shadow: var(--ui-dp-2);
	position: relative;
}

.depth4 {
	-webkit-box-shadow: var(--ui-dp-4);
	        box-shadow: var(--ui-dp-4);
}

.content {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
	-ms-flex-negative: 1;
	    flex-shrink: 1;
}

.content:not(:empty) {
	padding: calc(var(--line) * 1.5) var(--lineHalf) var(--lineHalf);
}

.dark { }
